@font-face {
    font-family: "Ubuntu Light";
    src: url(../fonts/Ubuntu-Light.eot);
    src: url(../fonts/Ubuntu-Light.eot?#iefix) format("embedded-opentype"),
      url(../fonts/Ubuntu-Light.woff) format("woff"),
      url(../fonts/Ubuntu-Light.ttf) format("truetype"),
      url(../fonts/Ubuntu-Light.svg#Ubuntu-Light) format("svg");
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: "Ubuntu Light Italic";
    src: url(../fonts/Ubuntu-LightItalic.eot);
    src: url(../fonts/Ubuntu-LightItalic.eot?#iefix) format("embedded-opentype"),
      url(../fonts/Ubuntu-LightItalic.woff) format("woff"),
      url(../fonts/Ubuntu-LightItalic.ttf) format("truetype"),
      url(../fonts/Ubuntu-LightItalic.svg#Ubuntu-LightItalic) format("svg");
    font-weight: 300;
    font-style: italic;
}

@font-face {
    font-family: "Ubuntu Regular";
    src: url(../fonts/Ubuntu-Regular.eot);
    src: url(../fonts/Ubuntu-Regular.eot?#iefix) format("embedded-opentype"),
      url(../fonts/Ubuntu-Regular.woff) format("woff"),
      url(../fonts/Ubuntu-Regular.ttf) format("truetype"),
      url(../fonts/Ubuntu-Regular.svg#Ubuntu-Regular) format("svg");
    font-weight: 400;
    font-style: normal;
}

@font-face {
  font-family: "Ubuntu Regular Italic";
  src: url(../fonts/Ubuntu-Italic.eot);
  src: url(../fonts/Ubuntu-Italic.eot?#iefix) format("embedded-opentype"),
    url(../fonts/Ubuntu-Italic.woff) format("woff"),
    url(../fonts/Ubuntu-Italic.ttf) format("truetype"),
    url(../fonts/Ubuntu-Italic.svg#Ubuntu-Italic) format("svg");
  font-weight: 400;
  font-style: italic;
}

@font-face {
    font-family: "Ubuntu Medium";
    src: url(../fonts/Ubuntu-Medium.eot);
    src: url(../fonts/Ubuntu-Medium.eot?#iefix) format("embedded-opentype"),
      url(../fonts/Ubuntu-Medium.woff) format("woff"),
      url(../fonts/Ubuntu-Medium.ttf) format("truetype"),
      url(../fonts/Ubuntu-Medium.svg#Ubuntu-Medium) format("svg");
    font-weight: 500;
    font-style: normal;
  }

@font-face {
  font-family: "Ubuntu Bold";
  src: url(../fonts/Ubuntu-Bold.eot);
  src: url(../fonts/Ubuntu-Bold.eot?#iefix) format("embedded-opentype"),
    url(../fonts/Ubuntu-Bold.woff) format("woff"),
    url(../fonts/Ubuntu-Bold.ttf) format("truetype"),
    url(../fonts/Ubuntu-Bold.svg#Ubuntu-Bold) format("svg");
  font-weight: 700;
  font-style: normal;
}


